.navbar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 10vh;
    padding: 15px;
    background-color: white;
    width: 100%;
    position: sticky;
    top: 0;
}

.logo {
    width: 75px;
    height: 75px;
    background-color: black;
    /* position: absolute; */
    /* top: 15px; */
    /* left: 15px; */
}

.links {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
}

.links li {
    margin-right: 10px;
}

@media screen and (min-width: 668px) {
    .navbar {
        height: 10vh;
    }
}